/* Header Styles */
body, html {
    margin: 0;
    padding: 0;
  }
  
  .SignInPage {
    margin: 0;
  }
  
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em 2em;
    background-color: var(--default-white);
  }
  
  header .logo img {
    height: 50px;
    width: auto;
    display: block;
  }
  
  header nav ul {
    list-style: none;
    display: flex;
    gap: 1.5em;
    margin: 0;
    padding: 0;
  }
  
  header nav ul li a, header nav ul li button {
    text-decoration: none;
    background: none;
    border: none;
    color: var(--default-black);
    font-size: 1rem;
    cursor: pointer;
  }
  
  header nav ul li a:hover, header nav ul li button:hover {
    text-decoration: underline;
    color: var(--default-color);
  }
  
  header nav ul li a.btn {
    padding: 0.5em 1em;
    background-color: var(--default-color);
    color: var(--default-white);
    border-radius: 5px;
  }
  
  header nav ul li a.btn:hover {
    background-color: var(--default-lighter-color);
  }
  header nav ul li button {
    background: none; /* Remove button background */
    border: none; /* Remove border */
    color: var(--default-black); /* Match the color of other links */
    font-size: 1rem; /* Match font size with other links */
    cursor: pointer; /* Change cursor to pointer for consistency */
    padding: 0; /* Remove padding to match links */
    text-decoration: none; /* Ensure no underline by default */
  }
  
  header nav ul li button:hover {
    text-decoration: underline; /* Add hover underline like other links */
    color: var(--default-color); /* Optional: Add hover color */
  }
  
  header nav ul li {
    display: inline-block; /* Ensure all list items align properly */
  }
