/* Footer Styles */
.footer {
  background-color: var(--default-color);
  color: var(--default-white);
  padding: 1.5rem; /* Reduce overall padding */
  text-align: center;
}

.newsletter h3 {
  font-size: 2.5rem; /* Slightly smaller font size */
  margin-bottom: 0.5rem; /* Reduce space below the heading */
  /*bold*/
}

.newsletter p {
  font-size: 1em;
  margin: 0.3rem 0; /* Reduce vertical spacing between paragraphs */
}

.newsletter input {
  padding: 0.8em; /* Increase padding for a bigger input box */
  width: 350px; /* Make the input wider */
  border-radius: 10px; /* Add rounded corners */
  border: 1px solid var(--default-white); /* Optional: Add a border */
  font-size: 1rem; /* Ensure the font size is proportional */
  box-sizing: border-box; /* Ensure padding doesn't affect width */
}

.social-links {
  margin-top: 0.5rem; /* Reduce spacing above social links */
}

.social-links a {
  margin: 0 0.8em; /* Slightly reduce horizontal spacing between icons */
  font-size: 2em;
}

.credit {
  margin-top: 0.5rem; /* Reduce spacing above the credit line */
  font-size: 0.8em;
}
