/* Profile Page Styles */
.ProfilePage {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2em;
  }
  
  .ProfilePage h2 {
    font-size: 2em;
    margin-bottom: 1.5em;
  }
  
  .ProfilePage form {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 500px;
  }
  
  .ProfilePage label {
    font-size: 1rem;
    margin-bottom: 0.5em;
    color: var(--default-color);
  }
  
  .ProfilePage input[type="text"],
  .ProfilePage input[type="email"],
  .ProfilePage input[type="password"] {
    width: 100%;
    padding: 0.8em;
    margin-bottom: 1.5em;
    border: 1px solid var(--default-color);
    border-radius: 5px;
  }
  
  .ProfilePage input[type="checkbox"] {
    margin-right: 0.5em;
  }
  
  .ProfilePage button {
    padding: 0.8em;
    background-color: var(--default-color);
    color: var(--default-white);
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
  }
  
  .ProfilePage button:hover {
    background-color: var(--default-lighter-color);
  }
  
  .ProfilePage .success {
    color: green;
    margin-top: 1em;
  }
  
  .ProfilePage .error {
    color: red;
    margin-top: 1em;
  }
  .ProfilePage .logout-btn {
    margin-top: 1.5em;
    padding: 0.8em;
    background-color: var(--default-lighter-color);
    color: var(--default-white);
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
  }
  
  .ProfilePage .logout-btn:hover {
    background-color: var(--default-color);
  }
  